























import ut from '../utils/index.js';
export default {
  props: ["orderId","reload","changeVisibileModal","confirmPlanlist"],
  data() { 
	return {
		check:false
	}
  },
	onLoad() {
		console.log(this.confirmPlanlist)
	},
  methods: {  
	againPlan() {
		ut.request({
			data: {
				orderId: this.orderId
			},
			url: "service/order/againPrice"
		}).then(data=>{
			this.$parent.changeVisibileModal(false)
			this.$emit('reload');
			ut.totast("操作成功")
			console.log(data)
		})
	},
	cg_check(){
		this.check=!this.check;
	},
	orderCheck() {
		if(!this.check){
			ut.totast("请确认验收完成");
			return;
		}
		ut.request({
			data: {
				orderId: this.orderId
			},
			url: "service/order/check"
		}).then(data=>{
			this.$parent.changeVisibileModal(false)
			this.$emit('reload');
			ut.totast("操作成功")
			console.log(data)
		})
	}
  }
}



