
















	import orderItem from '../../components/orderItem.vue'
	import ut from '../../utils/index.js';
	export default {
		components: {
            orderItem
        },
		data() {
			return {
				order_list: [],
				cancel_reason: [],
				type: ''
			}
		},
		onLoad(option) {
			this.type = option.type;
			let barTitle = "查看订单";
			if(this.type == 1) {
				barTitle = "查看订单";
			}else if(this.type == 2) {
				barTitle = "确认施工方案和价格";
			}else if(this.type == 3) {
				barTitle = "质量验收及付尾款";
			}
			wx.setNavigationBarTitle({
			  title: barTitle
			})
			this.init();
		},
		methods: {
			init() {
				this.getOrderList();
			},
			getOrderList() {
				ut.request({
					data: {
						type: this.type
					},
					method: 'get',
					url: "service/order/list"
				}).then(data=>{
					console.log(data)
					this.order_list = data;
				})
			}
		}
	}



